import { Component, OnInit, Injector, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material';
import { MatDialogRef } from "@angular/material";

import { UserService } from '../../../../api/services/user.service';
import { ApartmentService } from '../../../../api/services/apartment.service';
import { AccountsService } from '../../../.././api/services/accounts.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';


@Component({
  selector: 'app-income-view-credit',
  templateUrl: './income-view-credit.component.html',
  styleUrls: ['./income-view-credit.component.scss']
})
export class IncomeViewCreditComponent implements OnInit {
  
  isCreditNoteLoaded:boolean = false;

  creditNoteData:string = "";
  creditNoteDataList:any;

  custInvoiceDataList:any;
  userDataList:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 20;

  unitFieldType:string = "unitno";
  unitOrder:boolean = false;

  isMobile:boolean = false;

  apartmentBlockUnitId:any;

  constructor(
  	private injector: Injector,
    public dialog: MatDialog,
    private userService:UserService,
  	private apartmentService:ApartmentService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
  	return this.totalItems == 0 ? true : false;
  }

  getCustomerName(id){
  
    var data = _.filter( this.userDataList, function( item ) {

      if(item.userId === id)
      return item;

    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].firstName+' '+data[0].lastName;
    }

  }

  getBlockDetails(id){

	var data = _.filter( this.custInvoiceDataList, function( item ) {

		if(item.invoiceGLAccounts[0].invoiceId === id)
		return item;

	});

	if(data === undefined || data.length == 0){
		return '';
	}
	else {
		return data[0].apartmentBlockNumber+' '+data[0].apartmentBlockUnitNumber;
	}

  }

  getEmailStatus(value){
  	if(value){
  		return 'Y'
  	}
  	else {
  		return 'N'
  	}
  }

  getDate(date){
  	return moment(date).format("DD/MM/YYYY"); 
  }

  updateCreditItem(credit){


    setTimeout(() => {

      this.isCreditNoteLoaded = false;

      var details = {

        "creditNoteId": parseInt(credit.creditNoteId),
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "amount": parseInt(credit.amount),
        "collectionId": parseInt(credit.collectionId),
        "invoiceId": parseInt(credit.invoiceId),
        "isEmailSent": credit.isEmailSent,
        "isSmssent": credit.isSmssent,
        "comment": credit.comment,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString(),
        "transReference1": "string",
        "transReference2": "string",
        "glAcctIndicator": "Income",
        "apartmentBlockUnitId": parseInt(credit.apartmentBlockUnitId)

      }


      this.accountsService.updateCreditNote(details).subscribe((res:any) => {

        if(res.message){

          this.isCreditNoteLoaded = true;
          this.sharedService.setAlertMessage("Credit Note updated successfully");

        }
        else {
          this.isCreditNoteLoaded = true;
        }


      },
      error => {
        this.isCreditNoteLoaded = true;
      });

    }, 1000); 

  }

  ngOnInit() {

    this.userDataList = [];

    this.userService.getAllUsers().subscribe((res:any) => {

      /* this.userDataList = res.filter(item => {
        return item.apartmentId == parseInt(this.cookieService.get('apartmentId'))
      }); */

      this.userDataList = res;

      console.log(this.userDataList);
      
    })

  	this.accountsService.getAllCreditNotes().subscribe((res:any) => {

  		this.creditNoteDataList = res.filter(item => {
  			return (item.apartmentId == parseInt(this.cookieService.get('apartmentId')) && item.isActive)
  		});

  		this.totalItems = this.creditNoteDataList.length;

  		if(this.totalItems>this.itemLimit){
			this.ItemEndIndex = this.itemLimit;
		}
		else {
			this.ItemEndIndex = this.totalItems;
		}

  	this.isCreditNoteLoaded = true;

    this.accountsService.getCustInvoicesByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.custInvoiceDataList = res.filter(item => {
        return item.isActive;
      });


    }, error => {
        console.log(error);
    });


  	}, error => {
    	console.log(error);
    });

  }

}
